import "./icon.scss";

export enum Icons {
  Heart = "heart",
}
export enum Size {
  Tiny = "tiny",
}

// prettier-ignore
const Base64 = {
  [Icons.Heart]: "",
};

interface IconProperties {
  icon: Icons;
  size?: Size;
}

export const Icon = ({ icon, size }: IconProperties) => {
  return (
    <img
      className={"icon" + (size === Size.Tiny ? " icon--tiny" : "")}
      src={"data:image/svg+xml;base64, " + Base64[icon]}
    />
  );
};
